@font-face {
  font-family: "Font Awesome 5 Free";
  src: url("http://db.onlinewebfonts.com/t/061b39e95e1a0bd25e8d294af8596b2a.eot");
  src: url("http://db.onlinewebfonts.com/t/061b39e95e1a0bd25e8d294af8596b2a.eot?#iefix")
      format("embedded-opentype"),
    url("http://db.onlinewebfonts.com/t/061b39e95e1a0bd25e8d294af8596b2a.woff2")
      format("woff2"),
    url("http://db.onlinewebfonts.com/t/061b39e95e1a0bd25e8d294af8596b2a.woff")
      format("woff"),
    url("http://db.onlinewebfonts.com/t/061b39e95e1a0bd25e8d294af8596b2a.ttf")
      format("truetype"),
    url("http://db.onlinewebfonts.com/t/061b39e95e1a0bd25e8d294af8596b2a.svg#Font Awesome 5 Free")
      format("svg");
}

@font-face {
  font-family: "Daily Challenge";
  src: url("/public/assets/fonts/DailyChallenge.otf");
}

@font-face {
  font-family: "Benedict";
  src: url("/public/assets/fonts/Benedict\ Regular.otf");
}

@font-face {
  font-family: "RalewayMedium";
  src: url("/public/assets/fonts/raleway/Raleway-Medium.ttf");
}

@font-face {
  font-family: "CollegeHalo";
  src: url("/public/assets/fonts/College\ Halo.ttf");
}

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .ol {
    border: 0px solid red;
  }

  .w-vh-sm {
    width: 10vh;
  }
  .w-vh-md {
    width: 25vh;
  }

  .h-vm-100 {
    height: 100vmax;
  }

  .font-family-free {
    font-family: "FontAwesome";
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.testimonial .description::before {
  /* content: "\f10e"; */
  content: url("data:image/svg+xml;base64,");
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  display: inline-block;
  float: left;
  padding: 0 15px 5px 0;
  font-size: 30px;
  color: #ff5a5f;
  text-align: center;
  position: relative;
  top: 0px;
  left: 0;
}

.show_bg_2 {
  background-image: linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0),
      rgba(255, 255, 255, 0.932)
    ),
    url("/public/assets/images/hero.jpeg");
  background-size: cover;
  width: 100%;
  color: blue;
}

.underline_swirl::after {
  content: url("/public/assets/images/underlineswirl.png");
  display: block;
  width: 1rem;
  padding-top: 3px;
  border-bottom: 2px solid #f9dd94;
}

.phone_transform {
  transform: perspective(5916px) rotateX(37deg) rotateY(-57deg) rotateZ(38deg)
    scale(0.28) scaleX(2.25) scaleY(1.09);
  right: -94px;
  top: -338px;
  border-radius: 49px;
}
